<template>
  <span>
    {{ formattedValue }}
  </span>
</template>

<script>
  export default {
    name: "Currency",
    props: {
      value: {
        required: true,
        type: [Number, String],
        default: 0
      },
    },
    computed: {
      formattedValue: function() {
        return new Intl.NumberFormat("us-EN", {
          style: "currency",
          currency: "USD",
        }).format(parseFloat(this.value));
      },
    }
  };
</script>



